export default {
	props: {
		background:{
			type : String,
			default : "#F8F8F8"
		},
		height:{
			type : String,
			default : "44px"
		},
	},
	data(){
		return{
			top : 0	
		}
	},
	created:function(){
		var res = uni.getSystemInfoSync();
		this.top = res.statusBarHeight  + 'px';
	}
}
